@import url(https://fonts.googleapis.com/css2?family=Yanone+Kaffeesatz:wght@300;500;600;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: "Yanone Kaffeesatz", sans-serif;
  background-image: url(/Practice-React-Project/static/media/2930341.f3d64122.jpg);
  background-size: cover;
}

body {
  margin: 0;
}

.Card_card__3iVZZ {
  background: #fff;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.3);
  border-radius: 15px;
  /*not in course*/
  width: 250px;
  height: 120px;
  fill-opacity: 0.2;
  margin-top: 60px;
  display: block;
  margin-left: auto;
  margin-right: auto;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.Card_card__3iVZZ label {
  display: flex;
  justify-content: center;
  align-content: center;
  line-height: 19px;
}

.Button_button__2onP1 {
  font: inherit;
  border: 1px solid #a0c4ff;
  border-radius: 15px;
  background: #a0c4ff;
  color: white;
  font-weight: 400;
  padding: 0.35rem 1rem;
  cursor: pointer;
  margin-top: 8px;
  width: 80px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.3);
  transition: width 3s;
  transition: cubic-bezier(0.1, 0.7, 1, 0.1);
}

.Button_button__2onP1:hover,
.Button_button__2onP1:active {
  background: #bdb2ff;
  border-color: #bdb2ff;
  width: 95px;
}

.Button_button__2onP1:focus {
  outline: none;
}

.AddUser_input__3FvWR {
  margin: 2rem auto;
  padding: 1rem;
  width: 90%;
  max-width: 40rem;
}

.AddUser_input__3FvWR label {
  display: block;
  font-weight: 400;
  margin-bottom: 0.5rem;
}

.AddUser_input__3FvWR input {
  font: inherit;
  display: block;
  width: 100%;
  border: 1px solid #bdb2ff;
  padding: 0.15rem;
  margin-bottom: 0.5rem;
}

.AddUser_input__3FvWR input:focus {
  outline: none;
  border-color: #bdb2ff;
}

.UsersList_users__wk7-N {
  margin: 2rem auto;
  width: 90%;
  max-width: 40rem;
}

.UsersList_users__wk7-N ul {
  list-style: none;
  padding: 1rem;
}

.UsersList_users__wk7-N li {
  border: 1px solid #ccc;
  margin: 0.5rem 0;
  padding: 0.5rem;
}

